html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-second);
  background: var(--white);
  color: var(--black);
}

.visually-hidden {
  position: absolute;
  left: -625rem;
  top: auto;
  width: 0.0625rem;
  height: 0.0625rem;
  overflow: hidden;
}

.nav-item.active a {
  color: var(--red);
}

.arrow {
  border: solid var(--white);
  border-width: 0 0.1875rem 0.1875rem 0;
  display: inline-block;
  padding: 0.1875rem;
  margin: 0 0.3125rem;
  transition: all 0.3s linear;
  cursor: pointer;
}
.arrow:hover {
  border-color: var(--red);
}

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

:root {
  --red:#D12B34;
  --white:#FFFFFF;
  --dark-white:#ECEBEB;
  --black:#1C1919;
  --white-bg:#fcfcfc;
  --white-border:#e6e6e6;
  --light-red:#e73941;
}

:root {
  --font-main: "Dancing Script", cursive;
  --font-second: "Roboto", sans-serif;
}

.container {
  max-width: 75rem;
  width: 90%;
  margin: 0 auto;
}

.page-pd {
  padding: 8rem 0;
}

ul {
  margin: 0;
}

.nav-item {
  font-size: 0.875rem;
  font-weight: 500;
}

.logo {
  font-size: 1.5rem;
  font-family: var(--font-main);
  color: var(--white);
}

.main-title {
  font-size: 8.75rem;
  color: var(--white);
  font-family: var(--font-main);
  letter-spacing: 5px;
  margin: 0;
}

.main-content p {
  color: var(--white);
  letter-spacing: 1px;
  font-size: 1.375rem;
  margin-bottom: 2em;
}

.front-heading {
  text-align: center;
  margin: 0;
  font-size: 3.125rem;
  z-index: 2;
}

.sub-heading {
  color: var(--red);
  font-family: var(--font-main);
  font-size: 2.5rem;
  position: relative;
  z-index: 1;
  margin-bottom: -1.5625rem;
}

.center h3 {
  font-size: 1.375rem;
  margin: 0;
}

.meals-h h4 {
  margin: 0;
  font-size: 1.125rem;
}

.meals-f h4 {
  margin: 0;
  color: var(--red);
  font-size: 1.375rem;
}

.t-coment, .t-img, .t-name {
  z-index: 2;
  color: var(--white);
}

.testimonials .front-heading {
  color: var(--white);
  margin-bottom: 2.5rem;
}

.r-title {
  text-align: center;
  color: var(--white);
  font-size: 3.125rem;
  margin: 0 0 1.875rem 0;
}

.person {
  color: var(--white);
}
.person h1 {
  margin: 0 0 0.3125rem 0;
}
.person p {
  margin: 0 0 3.125rem 0;
}

.t-form .title {
  color: var(--white);
  font-size: 3.125rem;
}
.t-form .title h1 {
  margin: 0 0 6.25rem 0;
}
.t-form form .f-btn {
  font-size: 1.125rem;
}

.t-form ::placeholder {
  color: var(--dark-white);
  opacity: 1;
}

.contact-form ::placeholder {
  color: var(--black);
  opacity: 1;
}

textarea:focus, input:focus {
  outline: none;
}

nav {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
}

.navbar {
  width: 100%;
  min-height: 8vh;
  padding: 0.625rem 1.875rem;
  background-color: var(--black);
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: rgba(0, 0, 0, 0.1) 0rem 0.625rem 0.9375rem -0.1875rem, rgba(0, 0, 0, 0.05) 0rem 0.25rem 0.375rem -0.125rem;
}

.logo span {
  color: var(--red);
}

.nav-links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
}
.nav-links.container {
  max-width: 37.5rem;
  width: 100%;
  margin: 0 auto;
}

.nav-item a {
  color: var(--white);
  transition: color 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.nav-item a:hover {
  color: var(--red);
}

.contact {
  background: var(--red);
  border-radius: 100%;
  padding: 0.1875rem 0.6875rem 0.125rem 0.6875rem;
}
.contact svg {
  width: 1.5625rem;
}
.contact:hover {
  background: var(--white);
}
.contact:hover svg path {
  fill: red;
}

.btn-active {
  background: var(--white);
}
.btn-active svg path {
  fill: red;
}

.burger {
  display: none;
  cursor: pointer;
  padding: 10px 0 10px 10px;
}
.burger div {
  transition: all 1.2s cubic-bezier(0.075, 0.82, 0.165, 1);
  width: 35px;
  height: 3px;
  background: var(--white);
  border-radius: 100px;
}
.burger div:nth-child(1) {
  transform: translateY(-10px);
}
.burger div:nth-child(3) {
  transform: translateY(10px);
}
.burger:hover div:nth-child(1) {
  transform: translateY(-6px);
}
.burger:hover div:nth-child(3) {
  transform: translateY(6px);
}

.rotate {
  transform: rotateZ(90deg);
}

.links-toggle {
  transition: all 0.3s linear;
  background: #1c1919;
  height: 0;
  padding-bottom: 0;
  overflow: hidden;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 71.8125em) {
  .burger {
    display: block;
  }

  .nav-links {
    display: none;
  }

  .nav-active {
    height: 35vh;
    padding-bottom: 2rem;
  }

  .contact {
    display: none;
  }
}
main {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("images/mainBg.webp");
  background-color: var(--black);
  background-size: cover;
  background-repeat: no-repeat;
}
main::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #242424c4;
}

.main-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 2;
  text-align: center;
}

.explore-btn {
  color: white;
  background-color: #E52A34;
  padding: 1em 2em;
  border-radius: 100px;
  transition: all 0.3s linear;
}
.explore-btn:hover {
  background-color: white;
  color: #E52A34;
}

@media (max-width: 39.9375em) {
  .main-title {
    font-size: 5rem;
  }

  .main-content p {
    font-size: 1.125rem;
  }

  .explore-btn {
    padding: 0.8em 1.6em;
  }
}
.page-menu {
  min-height: calc(92vh - 2.3rem);
}

.sec-t {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 6rem;
}

.sec-c {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1.875rem;
}
.sec-c .heading-menu {
  position: relative;
  background-color: var(--white-bg);
  border: 1.5px dashed var(--white-border);
  padding: 3em 1.5em 0.5em 1.5em;
  transition: all 0.3s linear;
  height: fit-content;
}
.sec-c .heading-menu:hover {
  border: 1.5px dashed var(--red);
}
.sec-c .heading-menu .center {
  text-align: center;
  margin-bottom: 3.125rem;
}

.meals {
  display: flex;
  border-bottom: 1.5px dashed var(--white-border);
  transition: all 0.3s linear;
  padding-bottom: 0.625rem;
}
.meals .meals-txt {
  margin-left: 1.25rem;
  width: 100%;
}
.meals:hover .meals-h {
  color: var(--red);
}
.meals:hover {
  border-bottom: 1.5px dashed var(--red);
}

.center-meal {
  padding: 1.625rem 0 0.625rem 0;
  margin-bottom: 1.625rem;
}

.last-meal {
  border: none;
}
.last-meal:hover {
  border: none;
}

.meals-flex {
  display: flex;
  justify-content: space-between;
}
.meals-flex .meals-h {
  transition: all 0.3s linear;
  max-width: 7.3125rem;
  margin-right: 0.5rem;
}

.meals-in {
  max-width: 12.5rem;
}

.meals-img {
  width: 6.25rem;
  height: 6.25rem;
}
.meals-img img {
  width: 100%;
  border-radius: 6.25rem;
}

.menu-svg {
  position: absolute;
  top: 0;
  left: 0;
}
.menu-svg svg {
  fill: var(--white-border);
  width: 3.125rem;
  height: 3.125rem;
}

.full-menu {
  min-height: 100vh;
}

@media (max-width: 71.8125em) {
  .sec-c {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.875rem;
  }
}
@media (max-width: 45.625em) {
  .sec-c {
    display: grid;
    grid-template-columns: none;
    gap: 1.875rem;
  }
}
.testimonials {
  position: relative;
  min-height: calc(82vh - 2rem);
  background-image: url("images/t-background.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.testimonials::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #242424c4;
}
.testimonials .sec-t {
  padding: 0;
}

.t-coment {
  max-width: 31.25rem;
  text-align: center;
  font-size: 1.125rem;
  line-height: 1.875rem;
  margin-bottom: 2.5rem;
}

.t-img {
  width: 6.25rem;
  position: relative;
}
.t-img img {
  width: 100%;
  border-radius: 6.25rem;
}
.t-img span {
  padding-top: 2.1875rem;
  display: flex;
  position: absolute;
  right: -0.625rem;
  bottom: -0.625rem;
  font-size: 5rem;
  background-color: white;
  border-radius: 6.25rem;
  color: var(--red);
  width: 3.125rem;
  height: 3.125rem;
  align-items: center;
  justify-content: center;
}

.t-name {
  color: var(--dark-white);
}

.t-persons {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  flex-direction: row;
  z-index: 2;
}

.t-test {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.p-list button {
  background: rgba(255, 255, 255, 0.2);
  width: 15px;
  height: 15px;
  border-radius: 100px;
  border: 1px solid #4d4d4d;
}
.p-list .active {
  background: var(--red);
}

.t-arrow {
  margin-top: 1.25rem;
}

.reservation {
  min-height: 40vh;
  background: var(--red);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2.5rem 0;
}

.main-r {
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-r .sec-r {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.main-r .sec-r .explore-btn {
  background-color: rgba(0, 0, 0, 0);
  color: var(--white);
  border: 0.3125rem var(--white) solid;
}
.main-r .sec-r .explore-btn:hover {
  background-color: white;
  color: var(--red);
}

.t-form {
  min-height: 80vh;
  background-color: var(--red);
}
.t-form form .title {
  text-align: center;
}
.t-form form .placeholders {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3.125rem;
}
.t-form form .placeholders div {
  width: 18.75rem;
  height: 3.125rem;
}
.t-form form .form-control {
  width: 100%;
  height: 100%;
  padding-left: 0.625rem;
  background: transparent;
  border: 0.0625rem solid var(--white-border);
  color: var(--white);
  border-radius: 0.125rem;
}
.t-form form .f-btn {
  padding: 0;
  cursor: pointer;
  background: var(--white);
  color: var(--black);
  transition: all 0.3s linear;
}
.t-form form .f-btn:hover {
  color: var(--red);
}

#options {
  width: 100%;
  height: 100%;
  background: #00000000;
  border: 0.0625rem solid var(--white-border);
  border-radius: 0.125rem;
  color: var(--white);
}
#options option {
  background-color: var(--red);
}

input[type=time]::-webkit-calendar-picker-indicator {
  filter: invert(100%) sepia(4%) saturate(345%) hue-rotate(239deg) brightness(114%) contrast(85%);
  padding-right: 0.625rem;
}

input[type=date]::-webkit-calendar-picker-indicator {
  filter: invert(100%) sepia(4%) saturate(345%) hue-rotate(239deg) brightness(114%) contrast(85%);
  padding-right: 0.625rem;
}

.contact-p {
  min-height: 50vh;
  background-color: var(--black);
  color: white;
  padding-top: 3.125rem;
}

.row {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.row .a-row {
  max-width: 25rem;
  width: 90%;
  margin: 0 1.25rem 2.5rem 0;
}
.row .a-row p {
  color: var(--dark-white);
  margin: 1.875rem 0 3.125rem 0;
}
.row .a-row .row-ul {
  display: flex;
  padding: 0;
}
.row .a-row .row-ul .l-svg {
  background: var(--red);
  margin-right: 0.9375rem;
  padding: 0.5rem 0.5625rem 0.4375rem 0.5625rem;
  border-radius: 6.25rem;
  transition: all 0.3s linear;
}
.row .a-row .row-ul .l-svg svg {
  width: 1.5rem;
  height: 1.5rem;
  fill: var(--white);
}
.row .a-row .row-ul .l-svg:hover {
  background: var(--white);
}
.row .a-row .row-ul .l-svg:hover .p-svg {
  fill: var(--red);
}
.row .o-row {
  max-width: 18.75rem;
  width: 90%;
  margin: 0 1.25rem 2.5rem 2.5rem;
}
.row .o-row .row-ul {
  padding: 0;
  margin-top: 1.875rem;
}
.row .o-row .row-ul li {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.row .o-row .row-ul li .span2 {
  color: var(--dark-white);
}
.row .c-row .row-ul {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin-top: 1.875rem;
}
.row .c-row .row-ul a {
  color: var(--dark-white);
  margin-bottom: 0.9375rem;
}
.row .c-row .row-ul a:hover {
  color: var(--white);
}

@media (max-width: 56.25em) {
  .row .o-row {
    margin: 3.125rem 1.25rem 2.5rem 0rem;
  }

  .row .c-row {
    margin: 3.125rem 0rem 2.5rem 0rem;
  }
}
.choise {
  min-height: 75vh;
}
.choise .about-t {
  justify-content: flex-start;
  align-items: flex-start;
}
.choise .about-t .front-heading {
  text-align: left;
}
.choise .sec-c .heading-menu {
  padding: 1.25rem;
}
.choise .sec-c .heading-menu:hover {
  transition-delay: 0;
  transform: translateY(-0.625rem);
}
.choise .sec-c .heading-menu .number {
  font-weight: bold;
  color: var(--red);
  margin: 0;
}
.choise .sec-c .heading-menu h3 {
  margin: 0.625rem 0 1.875rem 0;
}

.chefs {
  min-height: 80vh;
  background-color: #F5F5F5;
  margin-bottom: 15.625rem;
  padding-bottom: 0;
}
.chefs .chefs__cont {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3.125rem;
  transform: translateY(6.25rem);
}
.chefs .chefs__cont .person {
  position: relative;
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: column;
  z-index: 1;
}
.chefs .chefs__cont .person::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: #000;
  background: linear-gradient(0deg, black 0%, rgba(0, 0, 0, 0) 100%);
}
.chefs .chefs__cont .person .row-ul {
  padding: 0.625rem 0rem 3.125rem 0rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.625rem;
}
.chefs .chefs__cont .person .row-ul .l-svg {
  background: var(--red);
  padding: 0.5rem 0.5625rem 0.4375rem 0.5625rem;
  border-radius: 100px;
  transition: all 0.3s linear;
}
.chefs .chefs__cont .person .row-ul .l-svg:hover {
  background: var(--white);
}
.chefs .chefs__cont .person .row-ul .l-svg:hover .p-svg {
  fill: var(--red);
}
.chefs .chefs__cont .person1 {
  background: var(--red) url("images/chef1.webp") no-repeat center;
  width: 360px;
}
.chefs .chefs__cont .person2 {
  background: var(--red) url("images/chef2.webp") no-repeat center;
  width: 360px;
}
.chefs .chefs__cont .person3 {
  background: var(--red) url("images/chef3.webp") no-repeat center;
  background-size: cover;
  width: 360px;
}

.contact-f {
  min-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact-f .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.contact-f .contact-info .info-c {
  display: flex;
  align-items: center;
  justify-content: normal;
  margin-bottom: 2.5rem;
}
.contact-f .contact-info .info-c i {
  background-color: var(--red);
  padding: 0.625rem 0.75rem 0.625rem 0.75rem;
  border-radius: 100%;
  margin-right: 0.9375rem;
}
.contact-f .contact-info .info-c i svg {
  width: 1.875rem;
  fill: var(--white);
}
.contact-f .contact-info .info-c .text-c p {
  margin: 0 0 0.3125rem 0;
}
.contact-f .contact-info .info-c .text-c p:last-child {
  margin: 0;
}
.contact-f .contact-form {
  width: 650px;
}
.contact-f .contact-form form {
  display: flex;
  align-items: center;
  justify-content: normal;
  flex-direction: column;
  align-items: normal;
  gap: 1.25rem;
}
.contact-f .contact-form form .fr1 {
  display: flex;
  align-items: center;
  justify-content: normal;
  align-items: normal;
  gap: 1.25rem;
}
.contact-f .contact-form form .fr1 input {
  width: 100%;
}
.contact-f .contact-form form #fname {
  height: 3.125rem;
}
.contact-f .contact-form form #tname {
  max-width: 12.5rem;
  cursor: pointer;
  background: var(--red);
  border: 3px solid var(--red);
  border-radius: 10px;
  color: var(--white);
  font-size: 1.125rem;
  padding: 0.9375rem 0;
  transition: all 0.3s linear;
}
.contact-f .contact-form form #tname:hover {
  background: var(--light-red);
  border: 3px solid var(--light-red);
  color: var(--white);
}
.contact-f .contact-form form .form-control {
  padding-left: 0.625rem;
  border: 0.0625rem solid var(--black);
  border-radius: 0.125rem;
  color: var(--black);
}

@media (max-width: 71.8125em) {
  .contact-f .container {
    flex-direction: column-reverse;
    align-items: flex-start;
  }
  .contact-f .contact-form {
    width: 100%;
    margin-bottom: 6.25rem;
  }
  .contact-f .contact-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .contact-f .contact-info .info-c {
    margin: 1.875rem 2.5rem 1.875rem 0;
  }
}