@use '../util' as *;

ul{
    margin: 0;
}


.nav-item{
    font-size: rem(14);
    font-weight: 500;
}

.logo{
    font-size: rem(24);
    font-family: var(--font-main);
    color: var(--white);
}

.main-title{
    font-size: rem(140);
    color: var(--white);
    font-family: var(--font-main);
    letter-spacing: 5px;
    margin: 0;
}

.main-content p{
    color: var(--white);
    letter-spacing: 1px;
    font-size: rem(22);
    margin-bottom: 2em;

}

.front-heading{
    text-align: center;
    margin: 0;
    font-size: rem(50);
    z-index: 2;
}


.sub-heading{
    color: var(--red);
    font-family: var(--font-main);
    font-size: rem(40);
    position: relative;
    z-index: 1;
    margin-bottom: rem(-25);
}

.center h3{
    font-size: rem(22);
    margin: 0;
}

.meals-h h4{
    margin: 0;
    font-size: rem(18);
}

.meals-f h4{
    margin: 0;
    color: var(--red);
    font-size: rem(22);
}

.t-coment,.t-img,.t-name{
    z-index: 2;
    color: var(--white);
}
.testimonials .front-heading{
    color: var(--white);
    margin-bottom: rem(40);
}

.r-title{
    text-align: center;
    color: var(--white);
    font-size: rem(50);
    margin: 0 0 rem(30) 0;
}



.person{
    color: var(--white);
    h1{
        margin: 0 0 rem(5) 0;
    }
    p{
        margin: 0 0 rem(50) 0;
    }
}

.t-form{
    .title{
        color: var(--white);
        font-size: rem(50);
        
        h1{
            margin: 0 0 rem(100) 0;
        }
    }
    form{
        .f-btn{
            font-size: rem(18);
        }
    }
}

.t-form{
    ::placeholder {
    color: var(--dark-white);
    opacity: 1;
    }
}
    
.contact-form{
    ::placeholder {
        color: var(--black);
        opacity: 1;
        }
}

textarea:focus, input:focus{
    outline: none;
}