@use '../util' as *;


.testimonials{
    position: relative;
    min-height: calc(90vh - 8vh - 2rem);
    background-image: url('images/t-background.webp');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    &::before{
        content: "";
        position: absolute;
        top: 0; left: 0;
        width: 100%; height: 100%;
        background: #242424c4;
        
    }
    .sec-t{
        padding: 0;
    }
    
}

.t-coment{
    max-width: rem(500);
    text-align: center;
    font-size: rem(18);
    line-height: rem(30);
    margin-bottom: rem(40);
}


.t-img{
    width: rem(100);
    position: relative;
    & img{
        width: 100%;
        border-radius: rem(100);
    }
    & span{
        padding-top: rem(35);
        display: flex;
        position: absolute;
        right: rem(-10);
        bottom: rem(-10);
        font-size: rem(80);
        background-color: white;
        border-radius: rem(100);
        color: var(--red);
        width: rem(50);
        height: rem(50);
        align-items: center;
        justify-content: center;
    }
}

.t-name{
    color: var(--dark-white);
}

.t-persons{
    @include flex(center);
    position: relative;
    flex-direction: row;
    z-index: 2;
}

.t-test{
    @include flex(center);
    flex-direction: column;
}

.p-list {
    & button{
        background: rgba(255, 255, 255, 0.2);
        width: 15px;
        height: 15px;
        border-radius: 100px;
        border: 1px solid #4d4d4d;
    }

    & .active{
        background: var(--red);
    }
}


.t-arrow{
    margin-top: rem(20);
}